
export function hash(str: string) {
    let hash = 0;
    if (str.length === 0) {
        return hash;
    }
    for (let i = 0; i < str.length; i++) {
        const char = str.charCodeAt(i);
        hash = ((hash<<5)-hash) /* same as hash *31 */ + char;
        hash = hash & hash; // Convert to 32bit integer
    }
    return hash;
}
